.visual {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0px;

	.inner {
		width: 100%;
		height: 100%;
		.bannerbox {
			position: absolute;
			width: 100%;
			height: 700px;
			display: block;
			background: url(../img/main3.jpg);
			position: relative;
			background-position: center;
			background-repeat: no-repeat;
			background-size: cover;

			.wrap {
				width: 100%;
				height: 100%;
				background: rgba(0, 0, 0, 0.7);
				position: absolute;

				.text {
					width: 1180px;
					height: 100%;
					margin: 0 auto;
					display: flex;
					justify-content: center;
					flex-direction: column;

					h2 {
						font: bold 15px/1 'arial';
						color: white;
						margin-bottom: 15px;
					}

					h3 {
						font: bold 50px/1.3 'arial';
						color: white;
					}
				}
			}
		}

		.dis {
			width: 1180px;
			height: 100%;
			margin: 0 auto;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			position: relative;
			margin-bottom: 50px;
			.box1 {
				height: 600px;
				margin-top: 100px;
				width: 580px;
				position: relative;
				display: flex;

				img {
					width: 100%;
					height: 100%;
				}

				.pic1 {
					position: absolute;
					left: 0px;
					top: 0px;
					height: 500px;
					width: 49%;
					background: pink;
					margin-right: 20px;
				}

				.pic2 {
					position: absolute;
					right: 0px;
					bottom: 0px;
					height: 500px;
					width: 49%;
					background: skyblue;
				}
			}

			.box2 {
				height: 600px;
				margin-top: 100px;
				width: 580px;
				position: relative;
				display: flex;
				flex-direction: column;
				justify-content: center;
				padding: 0px 100px;
				h2 {
					font: bold 15px/1.3 'arial';
					margin-bottom: 25px;
				}
				h3 {
					font: bold 40px/1.2 'arial';
					margin-bottom: 25px;
				}
				p {
					font: normal 15px/1.3 'arial';
					margin-bottom: 25px;
				}

				button {
					padding: 10px 10px;
					width: 150px;
					display: inline-block;
					border: 1px solid black;
					background: none;
					cursor: pointer;
					font: bold 15px/1.3 'arial';
				}
			}
		}

		.serv {
			width: 100%;
			height: 600px;
			background: black;
			margin: 0 auto;
			.wrap {
				padding-top: 100px;
				width: 1180px;
				margin: 0 auto;
				h2 {
					font: bold 15px/1.3 'arial';
					margin-bottom: 25px;
					color: White;
				}
				h3 {
					font: bold 40px/1.2 'arial';
					margin-bottom: 25px;
					color: White;
				}
			}
		}
	}
}
